$white: #fff;
$black: #111;
$gray: #f0f0f0;

$primary-color: #532167;
$primary-color2: #8f4fa9;

$red: #ff0000;
$yellow: #ffff00;
$blue: #0000ff;

@font-face{
    font-family: Poppins;
    src: url('./Poppins-Regular.ttf') format("truetype");
}

@font-face{
    font-family: Poppins-Bold;
    src: url('./Poppins-Bold.ttf') format("truetype");
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: Poppins;
    font-size: 16px;
}

.app {
    background: $gray;
    padding-top: 5.125rem;
    header {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        width: 100%;
        background: $white;
        border-bottom: 0.125rem solid $gray;
        .header-container {
            width: 100%;
            max-width: 64rem;
            margin: auto;
            padding: 0px 3.125rem;
            display: grid;
            grid-template-columns: 5rem 1fr;
            align-items: center;
            .logo-container {
                width: 100%;
                img {
                    display: block;
                    width: 100%;
                }
            }
            ul {
                margin-left: auto;
                display: grid;
                grid-auto-flow: column;
                grid-gap: 0.625rem;
                a {
                    text-decoration: none;
                    border-radius: 0.3125rem;
                    transition: .2s ease;
                    background: $white;
                    color: $black;
                    &:hover {
                        background: $gray;
                    }
                    &.contact-button {
                        background: $primary-color;
                        color: $white;
                        &:hover {
                            background: $primary-color2;
                        }
                    }
                    li {
                        padding: 0.9375rem;
                        list-style: none;
                    }
                }
            }
        }
    }
    #portfolio, #about {
        position: absolute;
        transform: translateY(-5.125rem);
        opacity: 0;
    }
    .portfolio {
        width: 100%;
        max-width: 64rem;
        margin: auto;
        padding: 1.25rem 3.125rem 2.5rem 3.125rem;
        .title {
            display: block;
            font-size: 1.25rem;
            font-family: Poppins-Bold;
            margin: 0.625rem 0px 1.875rem 0px;
            text-align: center;
        }
        .examples {
            display: grid;
            grid-template-columns: repeat(3, calc(33% - 0.4125rem));
            grid-auto-flow: row dense;
            align-items: flex-start;
            grid-gap: 1.25rem;
            .example {
                background: $white;
                padding: 1.25rem;
                border-radius: 0.3125rem;
                .image-container {
                    width: 100%;
                    img {
                        display: block;
                        width: 100%;
                    }
                }
                .name {
                    display: block;
                    font-size: 1.25rem;
                    font-family: Poppins-Bold;
                    margin: 0.625rem 0px;
                }
                .info {
                    line-height: 1.25rem;
                    letter-spacing: 0.125rem;
                }
            }
        }
    }
    .about {
        width: 100%;
        background: $white;
        padding: 1.25rem 3.125rem 1.25rem 3.125rem;
        .container {
            max-width: 64rem;
            margin: auto;
            .title {
                display: block;
                font-size: 1.25rem;
                font-family: Poppins-Bold;
                margin: 1.875rem 0px 1.875rem 0px;
                text-align: center;
            }
            .biography {
                line-height: 1.25rem;
                letter-spacing: 0.125rem;
                margin-bottom: 1.875rem;
            }
            .skills .skills-level {
                .subtitle {
                    margin: 1.25rem 0px;
                    font-size: 1.25rem;
                    text-align: center;
                    span {
                        font-family: Poppins-Bold;
                    }
                    span.high {
                        color: $red;
                    }
                    span.medium {
                        color: $yellow;
                    }
                    span.low {
                        color: $blue;
                    }
                }
                img {
                    display: inline-block;
                    width: 6.25rem;
                    margin: 0px 0.625rem 0.625rem 0px;
                }
            }
        }
    }
}

// TABLET
@media only screen and (max-width: 768px) {
    .app {
        header .header-container ul a {
            display: none;
            &.contact-button {
                display: block;
            }
        }
        .portfolio .examples {
            grid-template-columns: repeat(2, calc(50% - 0.625rem));
        }
    }
}

// MOVIL
@media only screen and (max-width: 425px) {
    .app {
        .portfolio .examples {
            grid-template-columns: 100%;
        }
    }
}